@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,400;0,500;0,600;0,700;1,800&display=swap);
body,html{height:100%;width:800vw;margin:0;background-color:#F5F5F5;font-family:'Fira Sans', sans-serif;overflow-x:hidden}.app__container{position:absolute;height:100%;min-height:100%;width:800vw;display:flex;flex-direction:row;transform:translateX(0px);transition:transform .3s ease-in-out}

.home__container{min-width:100vw;margin-top:130px;display:flex;align-items:center;flex-direction:column}.hero__text{color:#000;font-size:31px;text-align:center;font-weight:600;margin-bottom:50px;padding:0px 10px}.invite__text{color:#303030;font-size:24px;text-align:center;font-weight:500;margin:0}.invite__text:nth-child(3){margin-top:70px}

.templates__container{width:100vw;display:flex;align-items:center;flex-direction:column}.templates__img{height:400px;border:2px solid gray;border-radius:3px;transition:all .3s ease-in-out;margin:30px 0px}@media (min-width: 600px){.templates__img{height:700px}}.templates__img:hover{-webkit-filter:brightness(0.8);filter:brightness(0.8)}.templates__colors{display:flex;margin-bottom:30px}.templates__color{width:20px;height:20px;border-radius:20px;margin:0px 5px;border:3px solid #b6b5b5;transition:all .2s ease-in-out}.templates__color.selected{border:3px solid #000}.templates__title{font-weight:700;text-align:center;width:100vw;font-size:31px}#gray{background-color:#B5B5B5}#green{background-color:#90C999}#blue{background-color:#91BDC7}#yellow{background-color:#F1F37B}#brown{background-color:#AD8E7D}

.dot{display:block;background-color:white;border-radius:100%;border:2px solid #A4A4A4;width:12px;height:12px;margin:0px 5px}.dot.filled{background-color:#2B3A67}.dots{display:flex;flex-direction:row;margin-top:120px}

.personal__container{display:flex;flex-direction:column;align-items:center}.personal__title{font-weight:700;text-align:center;width:100vw;font-size:31px}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}.form{width:85%;display:flex;flex-direction:column}@media (min-width: 700px){.form{width:40vw}}.form__input{height:45px;min-height:45px;padding:0px 15px;outline:none;border:2px solid #B7B7B7;border-radius:5px;background-color:white}.form__input-error{color:#fa1212}.form__input-error.hidden{display:none}.form__input.hidden{display:none}.form__input.error{border:2px solid #fa1212}.form__label{margin:2px 0px;margin-top:15px;-webkit-user-select:none;-ms-user-select:none;-o-user-select:none;user-select:none}.form__controls{margin:60px 0px;display:flex;justify-content:space-between;width:85%}@media (min-width: 700px){.form__controls{width:40vw}}.photo__input{cursor:pointer;display:block;background-color:#C4C4C4;color:#3E3E3E;font-weight:500;text-align:center;border-radius:5px;width:100%;height:30px;padding:30px 0px;font-size:20px;-webkit-user-select:none;-ms-user-select:none;-o-user-select:none;user-select:none}.photo__name{text-align:center;color:#049e04}

.button{width:140px;height:50px;color:white;background-color:#2B3A67;border:0;border-radius:5px;font-family:'Fira Sans';transition:all .2s ease-in-out;font-size:20px}.button:hover{background-color:#16203D}.button.cancel{background-color:#ee5353;transition:all .2s ease-in-out}.button.cancel:hover{background-color:#cf3c3c}

.education__container{width:100vw;display:flex;flex-direction:column;align-items:center}.education__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.education__modal{z-index:10;width:100vw;position:fixed;height:100%;background-color:#F5F5F5;display:flex;justify-content:center;padding:50px 0px}.education__modal.hidden{display:none}.education__modal .form__controls{justify-content:space-between;width:100%}.plus__button{background-color:#C4C4C4;width:55px;height:55px;border-radius:35px;cursor:pointer}.plus__button::before{content:'';display:block;height:6px;background-color:white;width:25px;transform:translate(15px, 24px)}.plus__button::after{content:'';display:block;height:6px;background-color:white;width:25px;transform:translate(15px, 18px) rotate(90deg)}.school{width:85%;min-height:60px;border:2px solid #B7B7B7;border-radius:5px;padding:10px;background-color:white;margin-bottom:10px;cursor:pointer}@media (min-width: 700px){.school{width:40vw}}.school:hover{-webkit-filter:brightness(0.4);filter:brightness(0.4)}.school:hover::after{content:'DELETE';position:absolute;color:#383838;font-size:35px;text-align:center;width:100%;font-weight:bold;left:0;top:25%}.school__major{font-weight:600;margin:0;margin-bottom:2px}.school__name{margin:0}.school__years{display:flex;flex-direction:row;justify-content:space-between}.school__years .form__input{width:45%}.school__years-info{margin:0;margin-bottom:3px}

.work__start select:nth-child(2){margin-left:25px}.work__end select:nth-child(2){margin-left:25px}.work__container{display:flex;flex-direction:column;align-items:center}.work__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.work__modal{z-index:10;width:100vw;position:fixed;height:100vh;background-color:#F5F5F5;display:flex;justify-content:center;padding:50px 0px}.work__modal.hidden{display:none}.work__modal .form__controls{justify-content:space-between;width:100%}

.languages__container{display:flex;flex-direction:column;align-items:center}.languages__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.languages__modal{z-index:10;width:100vw;position:fixed;height:100vh;background-color:#F5F5F5;display:flex;justify-content:center;padding:50px 0px}.languages__modal.hidden{display:none}.languages__modal .form__controls{justify-content:space-between;width:100%}.language__info{display:flex;flex-direction:row;width:85%;background-color:white;border:2px solid #B7B7B7;border-radius:5px;justify-content:space-between;padding:10px;margin-bottom:20px}@media (min-width: 700px){.language__info{width:40vw}}.language__name{font-weight:600;text-align:left;margin:0}.language__level{text-align:right;margin:0}

.skills__container{display:flex;flex-direction:column;align-items:center}.skills__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.skills__modal{z-index:10;width:100vw;position:fixed;height:100vh;background-color:#F5F5F5;display:flex;justify-content:center;padding:50px 0px}.skills__modal.hidden{display:none}.skills__modal .form__controls{justify-content:space-between;width:100%}.skill__info{display:flex;flex-direction:row;width:85%;background-color:white;border:2px solid #B7B7B7;border-radius:5px;justify-content:space-between;padding:10px;margin-bottom:20px}@media (min-width: 700px){.skill__info{width:40vw}}.skill__name{font-weight:600;text-align:left;margin:0}

.download__container{margin-top:140px;display:flex;flex-direction:column;align-items:center}.download__container button{margin:50px 0px}.download__container a{display:block;text-decoration:none;color:white;height:100%;width:100%;text-align:center;line-height:48px}.download__title{font-weight:700;text-align:center;width:100vw;font-size:31px}

.logo{width:100vw;color:#2B3A67;font-size:35px;font-weight:800;text-align:center;position:absolute;top:0;left:0}

